.LandingPage-heading {
  text-transform: uppercase;
  color: #3e4152;
  letter-spacing: 4px;
  font-size: 28px;
  margin-top: 50px;
  text-align: center;
}
.poster-1 {
  grid-area: poster-1;
}
.poster-2 {
  grid-area: poster-2;
}
.poster-3 {
  grid-area: poster-3;
}
.LandingPage-container {
  width: 90%;
  margin: auto;
  display: grid;
  grid-template: "poster-1 poster-2 poster-3";
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
}
.Poster-container {
  position: relative;
  overflow: hidden;
  border-radius: 5px;
  height: 350px;
}
.Poster {
  max-width: 100%;
  z-index: 600;
  transition: all 0.3s;
}
.Poster-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 700;
  opacity: 0;
  /* display: flex;
  justify-content: center;
  align-items: center; */
  transition: all 0.3s;
}
.Poster-container:hover .Poster {
  -webkit-filter: grayscale() blur(3px);
          filter: grayscale() blur(3px);
}
.Poster-container:hover .Poster-overlay {
  opacity: 1;
}
.Overlay-button {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  text-align: center;
  letter-spacing: 1;
  text-transform: uppercase;
  padding: 10px 15px;
  border: none;
  border: 2px solid black;
  color: black;
  background-color: #fff;
  border-radius: 3px;
  transition: all 0.3s;
}
.Overlay-button:hover {
  background: rgb(0, 0, 0);
  color: #fff;
  border: 2px solid black;
}
@media (max-width: 850px) {
  .gutter {
    margin-top: 100px;
  }
}
@media (max-width: 700px) {
  .LandingPage-container {
    grid-template:
      "poster-1"
      "poster-2"
      "poster-3";
    grid-row-gap: 50px;
  }
  .Poster-container {
    margin: auto;
  }
}

